@import url('https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100..900;1,100..900&display=swap');

.App {
  text-align: center;
}

.Logo-holder{
  padding-top: 15px;
  padding-bottom: 10px;
}
.App-logo {
  border-style: solid;
  border-color: chocolate;
  pointer-events: none;
}

.App-header {
  padding: 62px;
  font-family: "Exo 2";

}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.Items {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}


.ItemName {
  font-family: 'Exo 2';
  font-size: 150%;

}

.Tile {
  position: relative;
  padding: 14px;
  margin-bottom: 20px;
  text-align: center;
  background-color: #eff0f2;
  border-radius: 100px;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
}


.Tile:hover, .Tile:focus, .Tile:active {
  -webkit-transform: rotate(2deg);
  transform: rotate(2deg);
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.Glow {
  width: 220px;
  height: 50px;
  border: none;
  outline: none;
  color: #fff;
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  padding-bottom: 6px;
  font-family: 'Exo 2';
  font-size: 24px;
}

.Glow:before {
  content: '';
  background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
  position: absolute;
  top: -2px;
  left:-2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity .3s ease-in-out;
  border-radius: 10px;
}

.Glow:active {
  color: #000
}

.Glow:active:after {
  background: transparent;
}

.Glow:hover:before {
  opacity: 1;
}

.glow-on-hover:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}

.ItemTable{
  width: 100%;
  border-spacing:0px;
  height: auto;
}
